'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { initializeDefaultData } from '@/lib/utils'

export default function SettingsPage() {
  const [currentPassword, setCurrentPassword] = useState('')
  const [newPassword, setNewPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [errors, setErrors] = useState<{ [key: string]: string }>({})
  const [alert, setAlert] = useState<{ type: 'success' | 'error', message: string } | null>(null)

  const validatePasswordForm = (): boolean => {
    const newErrors: { [key: string]: string } = {}

    if (!currentPassword.trim()) {
      newErrors.currentPassword = 'Password saat ini harus diisi'
    }

    if (!newPassword.trim()) {
      newErrors.newPassword = 'Password baru harus diisi'
    } else if (newPassword.length < 6) {
      newErrors.newPassword = 'Password baru minimal 6 karakter'
    }

    if (!confirmPassword.trim()) {
      newErrors.confirmPassword = 'Konfirmasi password harus diisi'
    } else if (confirmPassword !== newPassword) {
      newErrors.confirmPassword = 'Konfirmasi password tidak cocok'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handlePasswordChange = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validatePasswordForm()) return

    try {
      // Simulate password change (in a real app, this would call an API)
      setTimeout(() => {
        showAlert('success', 'Password berhasil diubah')
        setCurrentPassword('')
        setNewPassword('')
        setConfirmPassword('')
        setErrors({})
      }, 1000)
    } catch (error) {
      showAlert('error', 'Terjadi kesalahan saat mengubah password')
    }
  }

  const handleResetData = () => {
    if (!confirm('Apakah Anda yakin ingin mengatur ulang data? Tindakan ini akan menghapus semua data dan mengembalikan ke pengaturan awal.')) return

    try {
      // Clear all data from localStorage
      localStorage.removeItem('sipba_assets')
      localStorage.removeItem('sipba_categories')
      localStorage.removeItem('sipba_users')
      
      // Initialize default data
      initializeDefaultData()
      
      showAlert('success', 'Data berhasil diatur ulang ke pengaturan awal')
    } catch (error) {
      showAlert('error', 'Terjadi kesalahan saat mengatur ulang data')
    }
  }

  const handleExportAllData = () => {
    try {
      const assets = JSON.parse(localStorage.getItem('sipba_assets') || '[]')
      const categories = JSON.parse(localStorage.getItem('sipba_categories') || '[]')
      const users = JSON.parse(localStorage.getItem('sipba_users') || '[]')

      const exportData = {
        metadata: {
          exportedAt: new Date().toISOString(),
          system: 'SIPBA - Puskesmas Kresek',
          version: '1.0.0'
        },
        assets,
        categories,
        users
      }

      const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' })
      const url = URL.createObjectURL(blob)
      const link = document.createElement('a')
      
      link.href = url
      link.download = `sipba-backup-${new Date().toISOString().split('T')[0]}.json`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(url)

      showAlert('success', 'Data berhasil diekspor')
    } catch (error) {
      showAlert('error', 'Terjadi kesalahan saat mengekspor data')
    }
  }

  const showAlert = (type: 'success' | 'error', message: string) => {
    setAlert({ type, message })
    setTimeout(() => setAlert(null), 5000)
  }

  const getStorageStats = () => {
    try {
      const assets = JSON.parse(localStorage.getItem('sipba_assets') || '[]')
      const categories = JSON.parse(localStorage.getItem('sipba_categories') || '[]')
      const users = JSON.parse(localStorage.getItem('sipba_users') || '[]')

      return {
        totalAssets: assets.length,
        totalCategories: categories.length,
        totalUsers: users.length,
        totalSize: new Blob([
          localStorage.getItem('sipba_assets') || '',
          localStorage.getItem('sipba_categories') || '',
          localStorage.getItem('sipba_users') || ''
        ]).size
      }
    } catch (error) {
      return {
        totalAssets: 0,
        totalCategories: 0,
        totalUsers: 0,
        totalSize: 0
      }
    }
  }

  const storageStats = getStorageStats()

  return (
    <div className="space-y-6">
      {/* Alert */}
      {alert && (
        <Alert className={alert.type === 'error' ? 'border-red-200 bg-red-50' : 'border-green-200 bg-green-50'}>
          <AlertDescription className={alert.type === 'error' ? 'text-red-800' : 'text-green-800'}>
            {alert.message}
          </AlertDescription>
        </Alert>
      )}

      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Pengaturan</h1>
        <p className="text-gray-600 mt-2">
          Kelola pengaturan sistem dan preferensi pengguna
        </p>
      </div>

      {/* System Information */}
      <Card>
        <CardHeader>
          <CardTitle>Informasi Sistem</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <div>
                <h3 className="font-medium text-gray-900">Statistik Data</h3>
                <div className="mt-2 space-y-2 text-sm text-gray-600">
                  <p>Total Aset: {storageStats.totalAssets}</p>
                  <p>Total Kategori: {storageStats.totalCategories}</p>
                  <p>Total Pengguna: {storageStats.totalUsers}</p>
                  <p>Ukuran Penyimpanan: {(storageStats.totalSize / 1024).toFixed(2)} KB</p>
                </div>
              </div>
            </div>
            
            <div className="space-y-4">
              <div>
                <h3 className="font-medium text-gray-900">Informasi Aplikasi</h3>
                <div className="mt-2 space-y-2 text-sm text-gray-600">
                  <p>Nama: SIPBA - Sistem Informasi Pengelolaan Barang/Aset</p>
                  <p>Versi: 1.0.0</p>
                  <p>Lokasi: Puskesmas Kresek</p>
                  <p>Status: Produksi</p>
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Change Password */}
      <Card>
        <CardHeader>
          <CardTitle>Ubah Password</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handlePasswordChange} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <label htmlFor="currentPassword" className="text-sm font-medium">
                  Password Saat Ini *
                </label>
                <Input
                  id="currentPassword"
                  type="password"
                  value={currentPassword}
                  onChange={(e) => {
                    setCurrentPassword(e.target.value)
                    if (errors.currentPassword) setErrors({})
                  }}
                  placeholder="Masukkan password saat ini"
                  className={errors.currentPassword ? 'border-red-500' : ''}
                />
                {errors.currentPassword && (
                  <p className="text-sm text-red-600">{errors.currentPassword}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="newPassword" className="text-sm font-medium">
                  Password Baru *
                </label>
                <Input
                  id="newPassword"
                  type="password"
                  value={newPassword}
                  onChange={(e) => {
                    setNewPassword(e.target.value)
                    if (errors.newPassword) setErrors({})
                  }}
                  placeholder="Masukkan password baru"
                  className={errors.newPassword ? 'border-red-500' : ''}
                />
                {errors.newPassword && (
                  <p className="text-sm text-red-600">{errors.newPassword}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="confirmPassword" className="text-sm font-medium">
                  Konfirmasi Password *
                </label>
                <Input
                  id="confirmPassword"
                  type="password"
                  value={confirmPassword}
                  onChange={(e) => {
                    setConfirmPassword(e.target.value)
                    if (errors.confirmPassword) setErrors({})
                  }}
                  placeholder="Konfirmasi password baru"
                  className={errors.confirmPassword ? 'border-red-500' : ''}
                />
                {errors.confirmPassword && (
                  <p className="text-sm text-red-600">{errors.confirmPassword}</p>
                )}
              </div>
            </div>

            <Button type="submit">
              Ubah Password
            </Button>
          </form>
        </CardContent>
      </Card>

      {/* Data Management */}
      <Card>
        <CardHeader>
          <CardTitle>Manajemen Data</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div>
              <h3 className="font-medium text-gray-900 mb-3">Ekspor Data</h3>
              <p className="text-sm text-gray-600 mb-4">
                Ekspor semua data sistem ke file JSON untuk backup
              </p>
              <Button onClick={handleExportAllData} variant="outline">
                Ekspor Semua Data
              </Button>
            </div>

            <div className="border-t border-gray-200 pt-4">
              <h3 className="font-medium text-gray-900 mb-3">Reset Data</h3>
              <p className="text-sm text-gray-600 mb-4">
                Hati-hati! Tindakan ini akan menghapus semua data dan mengembalikan ke pengaturan awal.
                Data yang dihapus tidak dapat dikembalikan.
              </p>
              <Button onClick={handleResetData} variant="destructive">
                Reset Semua Data
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* System Preferences */}
      <Card>
        <CardHeader>
          <CardTitle>Preferensi Sistem</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div>
              <h3 className="font-medium text-gray-900 mb-2">Tampilan</h3>
              <p className="text-sm text-gray-600">
                Mode terang (light mode) - Sesuaikan melalui pengaturan browser Anda
              </p>
            </div>

            <div className="border-t border-gray-200 pt-4">
              <h3 className="font-medium text-gray-900 mb-2">Notifikasi</h3>
              <p className="text-sm text-gray-600">
                Notifikasi sistem akan ditampilkan di pojok kanan atas
              </p>
            </div>

            <div className="border-t border-gray-200 pt-4">
              <h3 className="font-medium text-gray-900 mb-2">Bahasa</h3>
              <p className="text-sm text-gray-600">
                Bahasa Indonesia - Default
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
